//AuthHeader
//JWT(Java Web Token)   

export default function authHeader() {
    //user   ( /) 
    // user ,   (null, undefined)
    let user = JSON.parse(localStorage.getItem("user"));

    //      
    if(user && user.token) {
        //springboot     
        return { Authorization: 'Bearer' + user.token };
        //node.js => return {'x-access-token' : user.accessToken};
    }else {
        return {};
    }



}